@import "https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --dark-blue: #2b3945;
  --very-dark-blue: #202c37;
  --light-mode-text: #111517;
  --dark-gray: #858585;
  --very-light-gray: #fafafa;
  --white: #fff;
}

body {
  background-color: var(--very-light-gray);
  color: var(--light-mode-text);
  font-family: Nunito Sans, sans-serif;
  line-height: 1.6;
  overflow-y: scroll;
}

input::placeholder {
  color: var(--dark-gray);
}

header {
  margin-bottom: 2.5rem;
  padding: 1.6rem 0;
  box-shadow: 0 0 .8rem #0003;
}

.nav-container {
  margin: 0 1rem;
}

.nav-content {
  max-width: 1400px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.nav-content h1 {
  font-size: 1rem;
  font-weight: 800;
}

.nav-content button {
  color: inherit;
  cursor: pointer;
  background-color: #0000;
  border: none;
  align-self: center;
  font-size: 12px;
  font-weight: bold;
}

.nav-content button i {
  margin-right: .4rem;
}

.grid-container {
  row-gap: 1rem;
  margin: 0 1rem;
  display: grid;
}

.grid-container.deactive, .parent-country-details-container {
  display: none;
}

.parent-country-details-container.active {
  display: block;
}

.search-box, .dropdown-box {
  margin-bottom: 1rem;
}

.search-box {
  max-width: 425px;
  height: 44px;
  background-color: var(--white);
  border-radius: 5px;
  align-items: center;
  padding: 18px;
  display: flex;
  box-shadow: 0 0 .8rem #0003;
}

.search-box i {
  font-size: 16px;
}

.search-box input {
  height: 40px;
  color: inherit;
  border: none;
  outline: none;
  flex: 1;
  padding-left: .8rem;
  font-size: 14px;
}

.select-box {
  width: 180px;
  flex-direction: column;
  display: flex;
}

.options-container {
  background-color: var(--white);
  width: 180px;
  z-index: 10;
  border-radius: 8px;
  margin-top: 3.2rem;
  display: none;
  position: absolute;
}

.options-container.active {
  display: block;
}

.selected {
  background-color: var(--white);
  border-radius: 8px;
  position: relative;
  box-shadow: 0 0 .8rem #0003;
}

.selected, .option h4 {
  cursor: pointer;
  padding: 12px 16px;
  font-size: 14px;
  font-weight: 600;
}

.option h4:hover {
  background-color: var(--very-light-gray);
}

.option h4.active:after {
  content: "*";
  margin-left: 1rem;
}

.fa-arrow-up {
  margin-left: 1rem;
  transition: all .6s;
}

.fa-arrow-up.active {
  transform: rotate(-180deg);
}

.grid-item {
  width: 100%;
  cursor: pointer;
  border-radius: 10px;
  justify-self: center;
  box-shadow: 0 0 1rem #0003;
}

.grid-item img {
  width: 100%;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.grid-item:hover {
  transform: translateY(-.5%);
  box-shadow: 0 1rem 1rem #00000080;
}

.content-details {
  padding: 1rem;
  font-weight: 600;
}

.content-details span {
  font-weight: 300;
}

.content-details h2 {
  margin-bottom: .6rem;
  font-size: 1.6rem;
}

.content-details > p {
  font-size: 1.1rem;
}

.content-details > p:last-child {
  margin-bottom: 1.6rem;
}

.details-container {
  flex-direction: column;
  margin: 0 1rem;
  display: flex;
}

.flag-container {
  flex-direction: column;
  align-items: flex-start;
  display: flex;
}

.back-btn {
  cursor: pointer;
  color: inherit;
  background-color: #0000;
  border: none;
  border-radius: 5px;
  align-self: start;
  margin-bottom: 2.5rem;
  padding: .6rem 1.8rem;
  box-shadow: 0 0 .4rem #0003;
}

.back-btn:hover {
  transform: translateY(-.5%);
  box-shadow: 0 0 .4rem #00000080;
}

.back-btn i {
  margin-right: .2rem;
}

.flag-container img {
  width: 100%;
  margin-bottom: 2rem;
}

.country-title {
  margin-bottom: 1.4rem;
  font-size: 1.6rem;
}

.country-details > p {
  margin-bottom: .4rem;
  font-weight: 600;
}

.country-details > p span {
  font-weight: 300;
}

.border-country-title {
  margin-bottom: 1rem;
  font-size: 1.1rem;
  font-weight: 600;
}

.country-neighbors {
  flex-wrap: wrap;
  gap: .6rem;
  margin-bottom: .6rem;
  display: flex;
}

.country-neighbors p {
  text-align: center;
  padding: .4rem .8rem;
  box-shadow: 0 0 .4rem #0006;
}

body, header, .grid-item {
  transition: background-color .4s;
}

.dark-mode-parent {
  background-color: var(--very-dark-blue);
}

.dark-mode-child {
  background-color: var(--dark-blue);
}

.spinner {
  text-align: center;
  margin: 5rem auto;
}

.spinner svg {
  height: 6rem;
  width: 6rem;
  background-color: #faebd7;
  border-radius: 50px;
  padding: 1rem;
  animation: rotate 4s linear infinite;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media only screen and (min-width: 425px) {
  .nav-content h1 {
    font-size: 1.6rem;
  }

  .nav-content button {
    font-size: 1rem;
  }

  .grid-item img {
    object-fit: cover;
  }

  .grid-item {
    max-width: 425px;
  }

  .nav-content h1 {
    margin-bottom: 0;
  }
}

@media only screen and (min-width: 768px) {
  .grid-container {
    grid-template-columns: 1fr 1fr;
    gap: 40px;
  }

  .search-box {
    width: 350px;
    align-self: center;
  }

  .dropdown-box {
    justify-self: end;
  }
}

@media only screen and (min-width: 1024px) {
  .grid-container {
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
  }

  .search-box {
    width: 450px;
    grid-area: 1 / 1 / 2 / -1;
  }

  .dropdown-box {
    grid-area: 1 / 3 / 2 / -1;
    justify-self: end;
  }
}

@media only screen and (min-width: 1440px) {
  .grid-container {
    width: 1400px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 30px;
    margin: 0 auto;
  }

  .parent-country-details-container {
    margin: 0 1rem;
  }

  .details-container {
    max-width: 1400px;
    grid-template-rows: 100px 1fr;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    margin: 0 auto;
    display: grid;
  }

  .back-btn {
    grid-column: 1 / -1;
    place-self: start;
  }

  .flag-container img {
    width: 600px;
    height: 400px;
    margin-bottom: 0;
  }

  .country-details-container {
    justify-self: end;
  }

  .detail-title {
    font-size: 1.8rem;
  }

  .country-details {
    grid-template-columns: 1fr 1fr;
    align-self: start;
    column-gap: 8rem;
    margin-bottom: 4rem;
    display: grid;
  }

  .border-countries {
    align-items: center;
    display: flex;
  }

  .border-countries h4 {
    margin-bottom: 0;
    margin-right: 1rem;
  }

  .country-neighbors {
    margin-bottom: 0;
  }
}

